<template>
  <div>
    <router-link class="header-abs" tag="div" to="/" v-show="showAbs">
      <span class="iconfont header-abs-back">&#xe682;</span>
    </router-link>
    <div :style="opacityStyle" class="header-fixed" v-show="!showAbs">
      <router-link tag="div" to="/">
        <div class="iconfont header-fixed-back">&#xe682;</div> </router-link
      >景点详情
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data() {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll() {
      const top = document.documentElement.scrollTop
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated() {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated() {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

